@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans JP", sans-serif;
  background: linear-gradient(0deg, #2273c3 0%, #fd2dad 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: #b8b8b8;
  -webkit-box-shadow: 0 0 0px 1000px rgba(0, 0, 0, 0) inset;
  transition: background-color 5000s ease-in-out 0s;
}

div {
  width: 100%;
}

.one {
  grid-area: user_image;
  background-color: #303841;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.one .self {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.one .self__user-img {
  position: relative;
}
.one .self__user-img img {
  height: 100px;
  width: 100px;
  border-radius: 100%;
  object-fit: cover;
  transition: 0.2s ease-in-out;
  margin: 45px 0 10px 0;
}
.one .self__user-img .online-state {
  background-color: #00c241;
  border-radius: 15px;
  border: 5px solid #303841;
  height: 13px;
  left: 150px;
  position: absolute;
  top: 120px;
  width: 13px;
}
.one .self__user-img .online-state .state-text {
  display: none;
  text-align: center;
  position: absolute;
  color: #d6d6d6;
  background: #161a20;
  width: auto;
  height: auto;
  font-size: 0.8rem;
  padding: 3px 5px;
  border-radius: 5px;
  top: -6px;
  left: 17px;
}
.one .self__user-img .online-state:hover .state-text {
  cursor: pointer;
  display: block;
}
.one .self__user-name {
  text-align: center;
  color: #d6d6d6;
  font-size: 1.2rem;
}
.one .self__user-email {
  text-align: center;
  color: #ffffff8c;
  font-weight: 400;
  grid-area: user_email;
}

.two {
  grid-area: edit_logout;
  background-color: #303841;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.two img {
  width: 30px;
  height: 30px;
  transition: 0.15s ease-in;
}
.two img:hover {
  opacity: 0.65;
  cursor: pointer;
}

.three {
  grid-area: search;
  background-color: #363e47;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.three .add-user {
  grid-area: add_user;
  position: relative;
}
.three .add-user form input {
  padding: 15px 35px;
  border: none;
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  font-size: 0.9rem;
  color: #c5c5c5;
  width: 65%;
}
.three .add-user form input:focus {
  color: #c5c5c5;
  outline: none;
}
.three .add-user form button {
  position: absolute;
  height: 35px;
  width: 45px;
  right: 38px;
  top: 5.5px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #9f9f9f;
  background-color: rgba(255, 255, 255, 0.15);
  border: none;
  border-radius: 20px;
}
.three .add-user form button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.four {
  grid-area: friends;
  background-color: #363e47;
  border-top: 3px solid #303841;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow-y: auto;
}
.four::-webkit-scrollbar {
  width: 0.5rem;
}
.four::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #2b2b2b;
}
.four::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.5);
  outline: none;
  border-radius: 2.5px;
}
.four a {
  text-decoration: none;
}
.four .user-container {
  display: grid;
  grid-template-columns: auto;
  grid-template-areas: "users_image users_info users_info";
  transition: 0.2s ease-in-out;
  border-bottom: 2px solid #303841;
}
.four .user-container:hover {
  background-color: #ffffff09;
  cursor: pointer;
}
.four .user-container .user-img {
  margin: 8px;
  padding: 0;
  align-self: center;
  justify-self: center;
  grid-area: users_image;
  position: relative;
}
.four .user-container .user-img img {
  width: 65px;
  height: 65px;
  object-fit: cover;
  border-radius: 45px;
}
.four .user-container .user-img .online-state {
  background-color: #00c241;
  border: 4px solid #303841;
  border-radius: 15px;
  bottom: 1px;
  height: 11px;
  left: 45px;
  position: absolute;
  width: 11px;
}
.four .user-container .user-img .offline-state {
  background-color: #aeaeae;
  border: 4px solid #303841;
  border-radius: 15px;
  bottom: 1px;
  height: 11px;
  left: 45px;
  position: absolute;
  width: 11px;
}
.four .user-container .user-wrapper {
  grid-area: users_info;
  align-self: center;
  justify-self: center;
  display: grid;
  grid-template-columns: auto;
  grid-template-areas: "users_info_name users_info_unread" "users_info_msg .";
}
.four .user-container .user-wrapper h4,
.four .user-container .user-wrapper h5 {
  margin: 0;
  padding: 0;
  width: 175px;
  text-align: start;
}
.four .user-container .user-wrapper h4 {
  grid-area: users_info_name;
  align-self: center;
  justify-self: self-start;
  font-size: 1rem;
  font-weight: 500;
  color: #d1d1d1;
}
.four .user-container .user-wrapper h5 {
  grid-area: users_info_msg;
  align-self: center;
  justify-self: self-start;
  font-size: 0.8rem;
  font-weight: 500;
  color: #9d9d9d;
  margin: 5px 0px;
}
.four .user-container .user-wrapper .unread-messages {
  grid-area: users_info_unread;
  align-self: center;
  justify-self: self-start;
  display: flex;
  justify-content: center;
  color: white;
  background-color: #1c2025;
  border-radius: 15px;
  height: 20px;
  width: 20px;
  display: none;
}
.four .user-container .user-wrapper span {
  align-self: center;
  justify-self: center;
  font-size: 0.8rem;
}

.five {
  min-height: 54.67px;
  grid-area: friend_name;
  margin: 15px;
  background-color: #303841;
  display: grid;
  align-content: center;
  justify-items: center;
  grid-template-columns: auto;
  grid-template-rows: auto;
  grid-template-areas: "user_name . . . ." "user_email user_email . . .";
  position: relative;
}
.five .user-name {
  grid-area: user_name;
  align-self: center;
  justify-self: self-start;
  color: #ffffffe8;
  font-size: 1.35rem;
  margin-left: 25px;
}
.five .user-email {
  grid-area: user_email;
  justify-self: self-start;
  color: #ffffff8c;
  font-weight: 400;
  margin-left: 25px;
}

.six {
  grid-area: chat_history;
  background-color: #303841;
  border-top: 2px solid #ffffff17;
  overflow: auto;
}
.six::-webkit-scrollbar {
  width: 0.5em;
}
.six::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #2b2b2b;
}
.six::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.5);
  outline: none;
  border-radius: 2.5px;
}
.six .message-received {
  justify-self: self-end;
}
.six .message-received .body {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.six .message-received .body span {
  background-color: #3e4650;
  text-align: start;
  margin: 20px 25px 0;
  padding: 10px 15px;
  max-width: 500px;
  color: #cacaca;
  border-radius: 1.25rem 1.25rem 1.25rem 0;
  overflow: hidden;
}
.six .message-received .author {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.six .message-received .author .name {
  text-align: start;
  color: #a3a3a4;
  margin: 5px 0px 5px 25px;
}
.six .message-received .author .time {
  text-align: start;
  margin: 5px 10px;
  color: #808080;
}
.six .message-sent {
  justify-self: self-end;
}
.six .message-sent .body {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.six .message-sent .body span {
  background-color: #2f4969;
  text-align: end;
  margin: 20px 25px 0;
  padding: 10px 15px;
  max-width: 500px;
  color: #cacaca;
  border-radius: 1.25rem 1.25rem 0 1.25rem;
  overflow: hidden;
}
.six .message-sent .author {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.six .message-sent .author .name {
  text-align: end;
  color: #a3a3a4;
  margin: 5px 25px 5px 0px;
}
.six .message-sent .author .time {
  text-align: end;
  margin: 5px 10px;
  color: #808080;
}

.seven {
  grid-area: send_message;
  background-color: #303841;
  border-top: 2px solid #ffffff17;
  display: flex;
  align-items: center;
  justify-content: center;
}
.seven form {
  display: grid;
  grid-template-columns: auto;
  grid-template-areas: "msg_input msg_input msg_input msg_input send_button";
}
.seven form input {
  grid-area: msg_input;
  color: #e9e9e9;
  width: 550px;
  font-size: 1.1rem;
  padding: 20px 10px;
  border: none;
  background-color: #303841;
}
.seven form input:focus {
  outline: none;
  color: #e9e9e9;
}
.seven form input::placeholder {
  color: #bababa;
}
.seven form button {
  grid-area: send_button;
  background-color: #4f5255;
  font-size: 1.1rem;
  font-weight: 600;
  color: #e9e9e9;
  border: none;
  padding: 10px 35px;
  max-height: 40px;
  align-self: center;
  border-radius: 8px;
  transition: 0.1s ease-in;
}
.seven form button:hover {
  transition: 0.1s ease-in;
  cursor: pointer;
  opacity: 0.8;
}

.wrapper {
  background-color: #303841;
  text-align: center;
  display: grid;
  align-content: center;
  justify-items: center;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-template-areas: "user_image user_image search search search friend_name friend_name friend_name friend_name friend_name friend_name" "user_image user_image friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" "user_image user_image  friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" "add_user add_user friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" ". . friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" "edit_logout edit_logout friends friends friends chat_history chat_history chat_history chat_history chat_history chat_history" "edit_logout edit_logout friends friends friends send_message send_message send_message send_message send_message send_message";
  border-radius: 7px;
  overflow: hidden;
  perspective: 1px;
  box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.433);
  height: 90%;
  width: 1400px;
}